      style="background-image: url('/svgrepo-com/star.svg')"
    ></div>
    <div class="mouseclick" :style="mouseclickstyle"></div>
    <div ref="onchart" style v-on:mousemove="showCoords($event)">
      <ve-line :data="chartData"></ve-line>
    </div>
    <div :style="styleUnderChart" style="position: relative;width:100%">
      <p>
        <span style="color: green">Random Line</span> - {{$t('RandomLine') }}
      </p>
      <p>
        <span style="color: blue">Mouse_Touch Line</span> - {{$t('MouseTouchLine') }}
        <img
          style="width:25px"
          src="/svgrepo-com/sring.svg"
        />
      </p>
