import flowinghh from "../flowinghh";
import $ from "jquery";
export default {
  components: { VeLine, flowinghh },
  props: {
    appWidth: Number,
    appHeight: Number,
    appHeightView: Number,
    isShow: Boolean
  },
  data() {
    return {
      styleUnderChart: {},
      counter: 1,
      interval: null,
      chartData: {
        columns: ["times", "Random", "Mouse_Touch"],
        rows: []
      },
      chartX: 0,
      chartY: 0,
      chartHeight: 0,
      isUser: false,
      makesure5: 14, //should have 5 in 30 times
      userNumber: 5,
      flowingbelowchartW: 0,
      flowingbelowchartH: 0,
      flowingNumber: 5,
      flowingNumberNew: 5,
      onmovingstyle: {
        width: "240px",
        height: "240px",
        top: "-1000px",
        left: "-1000px"
      },
      movingSteps: 0,
      showiconhowto: 0
    };
  },
  computed: {
    bchartRect() {
      return this.$refs.refbchart.getBoundingClientRect();
    },
    flowingsetting() {
      return {
        flowingW: this.flowingbelowchartW,
        flowingH: this.flowingbelowchartH,
        flowingN: this.flowingNumber
      };
    },
    mouseclickstyle() {
      if (this.showiconhowto < 2) {
        if (typeof window.orientation !== "undefined" || navigator.userAgent.indexOf("IEMobile") !== -1) {
          // console.log("----Mobile");
          return "background-image: url('/svgrepo-com/touch.svg')";
        } else {
          // console.log("----desktop");
          return "background-image: url('/svgrepo-com/mouse.svg')";
        }
      } else {
        return "visibility: hidden;";
      }
    }
  },
  mounted() {
    //--------------------------

    var _core = _interopRequireDefault(require("@keyframes/core"));

    function _interopRequireDefault(obj) {
      return obj && obj.__esModule
        ? obj
        : {
            default: obj
          };
    }

    var doForEach = function doForEach($el, cb) {
      $el.each(function(_, elem) {
        if (elem.Keyframes) {
          cb(elem.Keyframes);
        } else {
          elem.Keyframes = new _core["default"](elem);
          cb(elem.Keyframes);
        }
      });
    };

    $.keyframe = {
      isSupported: _core["default"].isSupported,
      generate: _core["default"].generate,
      generateCSS: _core["default"].generateCSS,
      define: _core["default"].define
    };

    $.fn.resetKeyframe = function(cb) {
      var $this = this;
      doForEach($this, function(kf) {
        return kf.reset().then(cb);
      });
    };

    $.fn.playKeyframe = function(frameOptions, callbacks) {
      const $this = this;
      let cb = callbacks;
      if (frameOptions.complete) {
        callbacks = frameOptions.complete;
      }
      if (typeof callbacks === "function") {
        cb = {
          onIteration: callbacks,
          onEnd: callbacks
        };
      }
      doForEach($this, kf => kf.play(frameOptions, cb));
