        this.chartData.rows.shift();
        var newNumber = (Math.random() * 5).toFixed(0);

        if (this.isUser) {
          this.isUser = false;
        }

        this.makesure5++;
        if ((newNumber != 5) & (this.makesure5 > 14)) {
          this.makesure5 = 0;
          newNumber = 5;
        } else if (newNumber == 5) {
          this.makesure5 = 0;
        }
        // console.log("-->makesure5-->",this.makesure5);

        this.chartData.rows.push({ times: this.counter.toString(), Random: newNumber, Mouse_Touch: this.userNumber });
        //

        this.counter++;
      }
      // }
    }, 500);
  },
  methods: {
    updatetopleft: function(value) {
      // console.log("=======emit========>", value);
      this.flowinghhTopLeft = value;
    },
    showCoords(event) {
      //var x = event.clientX;
      var y = event.clientY;
      //var coords = "X coords: " + x + ", Y coords: " + y;
      // var mm= this.$refs.refflowinghh.getBoundingClientRect();
      this.isUser = true;
      y = y - this.chartY;
      if (y < this.chartHeight * 0.2375) {
        this.userNumber = 5;
      } else if (y < this.chartHeight * 0.3625) {
        this.userNumber = 4;
      } else if (y < this.chartHeight * 0.4875) {
        this.userNumber = 3;
      } else if (y < this.chartHeight * 0.6125) {
        this.userNumber = 2;
      } else if (y < this.chartHeight * 0.7375) {
        this.userNumber = 1;
      } else {
        this.userNumber = 0;
      }

      if (this.flowingNumberNew != this.userNumber) {
        if (this.showiconhowto < 2) {
          this.showiconhowto++;
        }
        let chartrect = this.$refs.onchart.getBoundingClientRect();
        this.chartX = chartrect.x;
        this.chartY = chartrect.y;
        this.chartHeight = chartrect.height;
        this.flowingNumberNew = this.userNumber;

        this.interval = setTimeout(() => {
          this.flowingNumber = this.flowingNumberNew;
        }, 1000);

        this.onmovingstyle.top = y - this.chartY - 120 + "px";
        this.onmovingstyle.left = event.clientX - this.chartX - 120 + "px";

        let theTop = this.flowinghhTopLeft.top + 440 + "px";
        let theLeft = this.flowinghhTopLeft.left + 40 + "px";
        $(".movingstyle").resetKeyframe(function() {
          $.keyframe.define([
            {
              name: "ball-move",
              "100%": {
                // top: "600px",
                // left: "490px",
                top: theTop,
                left: theLeft,
                width: "20px",
                height: "20px",
                "-ms-transform": "rotate(1080deg)" /* IE 9 */,
                transform: "rotate(1080deg)",
                visibility: "hidden"
              }
            }
          ]);

          // move with easing
          $(".movingstyle").playKeyframe({
            // "position": "absolute",
            // "z-index": +1,
            name: "ball-move",
            duration: "1s",
            iterationCount: "1",
            visibility: "hidden"
          });
        });

        // $(".movingstyle").resetKeyframe(function() {
        //   $.keyframe.define([
        //     {
        //       name: "ball-move",
        //       "100%": {
        //         top: this.flowinghhTopLeft.top + 400 +"px",
        //         left:this.flowinghhTopLeft.left+"px"
        //       }
        //     }
        //   ]);

        //     $(".movingstyle").resetKeyframe(function() {
        //       $.keyframe.define([
        //         {
        //           name: "ball-move",
        //           "100%": {
        //   width: "85px",
        //   height: "85px",
        //   "-ms-transform": "rotate(360deg)", /* IE 9 */
        //   transform: "rotate(360deg)"
        // }
        //         }
        //       ]);

        //       // move with easing
        //       $(".movingstyle").playKeyframe({
        //         top:"100px",
        //         left:"100px",
        // "position": "absolute",
        // "z-index": +1,
        // "animation-name": "starmoving",
        // "animation-duration": "1s",
        // "animation-iteration-count": "infinite"

        //         name: "ball-move",
        //         duration: "3s",
        //         timingFunction: "ease",
        //         iterationCount: "1",
        // position: 'absolute',
        // 'z-index': +1,
        //           top: y - this.chartY + "px",
        //           left: event.clientX - this.chartX + "px",
        //   });

        // });
      }
    }

    // if (this.flowingNumber > 0) {
    //   this.onmovingstyle = {
    //     top: y - this.chartY + "px",
    //     left: event.clientX - this.chartX + "px"
    //   };
    //   this.movingSteps = 0;

    //   this.interval = setInterval(() => {
    //     this.movingSteps++;
    //     let movingRect = this.$refs.refmovingstyle.getBoundingClientRect();

    //     // this.flowinghhTopLeft
    //     let theYpx = movingRect.y - this.bchartRect.y;
    //     let theXpx = movingRect.x - this.bchartRect.x;
    //     let resultTop = ((( + theYpx) / 2 + theYpx) / 2 + theYpx) / 2;
    //     let resultLeft = (((this.flowinghhTopLeft.left + theXpx) / 2 + theXpx) / 2 + theXpx) / 2;
    //     if ((resultTop < 50) & (resultLeft < 50)) {
    //       clearInterval(this.interval);
    //       this.onmovingstyle = {
    //         visibility: "hidden"
    //       };
    //     } else {
    //       this.onmovingstyle = {
    //         top: resultTop + "px",
    //         left: resultLeft + "px"
    //       };
    //     }
    //     // console.log("==flow=====<", this.flowinghhTopLeft.left);
    //     // console.log("==onmoving=====<", movingRect);
    //   }, 1000);
