<template>
<div class="flowing"
    ref="onsvg"
    @click="userclick($event)"
    :style="styleflowing"
    style="width:100px;height:100px">
    <span
      style="  position: absolute; z-index: +1;top:32px;left:42px; font-size: 25px; color:blue;"
    >{{userNumber}}</span>
  <div
    class="flowingbackground"
    style="cursor: pointer;width:100px;height:100px;background-image: url('/svgrepo-com/sring.svg')"
  >
  </div>
  </div>
</template>

<script>
export default {
  props: {
    config: Object
  },
  data() {
    return {
      init: false,
      windpowerx: 0,
      windpowery: 0,
      stopPower: 2,
      flowpowerx: 0,
      flowpowery: 0,
      styleflowing: {}
    };
  },
  computed: {
    userNumber() {
      return this.config.flowingN;
    }
  },
  watch: {
    config() {
      if ((this.init == false) & (this.config.flowingW != 0)) {
        this.init = true;
        this.objTop = this.config.flowingH / 2;
        this.objLeft = this.config.flowingW / 2;

        this.styleflowing = {
          top: this.objTop + "px",
          left: this.objLeft + "px"
        };
      }
    }
  },
  methods: {
    userclick(event) {
      //var x = event.clientX;

      let svgrect = this.$refs.onsvg.getBoundingClientRect();
      let thex = event.clientX - svgrect.x;
      let they = event.clientY - svgrect.y;

      if (thex < 50) {
        if (they < 50) {
          this.windpowerx = this.windpowerx - -(Math.random() * 100).toFixed(0);
          this.objTop = this.objTop + 50;
          this.windpowery = this.windpowery - -(Math.random() * 100).toFixed(0);
          this.objLeft = this.objLeft + 50;
        } else {
          this.windpowerx = this.windpowerx - (Math.random() * 100).toFixed(0);
          this.objTop = this.objTop - 50;
          this.windpowery = this.windpowery - -(Math.random() * 100).toFixed(0);
          this.objLeft = this.objLeft + 50;
        }
      } else {
        if (they < 50) {
          this.windpowerx = this.windpowerx - -(Math.random() * 100).toFixed(0);
          this.objTop = this.objTop + 50;
          this.windpowery = this.windpowery - (Math.random() * 100).toFixed(0);
          this.objLeft = this.objLeft - 50;
        } else {
          this.windpowerx = this.windpowerx - (Math.random() * 100).toFixed(0);
          this.objTop = this.objTop - 50;
          this.windpowery = this.windpowery - (Math.random() * 100).toFixed(0);
          this.objLeft = this.objLeft - 50;
        }
      }
      //var coords = "X coords: " + x + ", Y coords: " + y;
      // console.log("------------->", thex, they);

        this.objTop = this.objTop - -this.flowpowerx;
        this.objLeft = this.objLeft - -this.flowpowery;
        if (this.objTop < 0) {
          this.objTop = 0;
          this.windpowerx = (Math.random() * 100).toFixed(0);
        }
        if (this.objLeft < 0) {
          this.objLeft = 0;
          this.windpowerxy = (Math.random() * 100).toFixed(0);
        }
        if (this.objTop > this.config.flowingH - 100) {
          this.objTop = this.config.flowingH - 100;
          this.windpowerx = -(Math.random() * 100).toFixed(0);
        }
        if (this.objLeft > this.config.flowingW - 100) {
          this.objLeft = this.config.flowingW - 100;
          this.windpowerx = -(Math.random() * 100).toFixed(0);
        }
        
      this.styleflowing = {
        top: this.objTop + "px",
        left: this.objLeft + "px"
      };
    }
  },
  mounted() {
    // console.log("flowinghh   config--> ", this.config);
    // console.log("--------------------->>",this.objTop,this.objLeft);

    // this.objTop=  this.config.belowchartH/2 ;
    // this.objLeft= this.config.belowchartW/2 ;
    // console.log("---QQQ------>>", this.config);

    this.interval = setInterval(() => {
      if (this.init) {
        // windpowerx: 0,  10> no wind > -10
        // windpowery: 0,
        // stopPower: 10,
        // flowpowerx: 0,
        // flowpowery: 0,

        //new wind power
        if ((this.windpowerx < 10) & (this.windpowerx > -10)) {
          this.windpowerx = (Math.random() * 100).toFixed(0);
          if ((Math.random() * 1).toFixed(0) < 1) {
            this.windpowerx = -this.windpowerx;
          }
        }
        if ((this.windpowery < 10) & (this.windpowery > -10)) {
          this.windpowery = (Math.random() * 100).toFixed(0);
          if ((Math.random() * 1).toFixed(0) < 1) {
            this.windpowery = -this.windpowery;
          }
        }
        if (this.windpowerx > 0) {
          this.flowpowerx = this.flowpowerx - -this.windpowerx * 0.02;
          if (this.flowpowerx > this.windpowerx) {
            this.flowpowerx = this.windpowerx;
          }
          if (this.flowpowerx > this.stopPower) {
            this.flowpowerx = this.flowpowerx - this.stopPower;
          } else if (this.flowpowerx < -this.stopPower) {
            this.flowpowerx = this.flowpowerx - -this.stopPower;
          }
          this.windpowerx = this.windpowerx - (Math.random() * 10).toFixed(0);
        } else {
          this.flowpowerx = this.flowpowerx + this.windpowerx * 0.02;

          if (this.flowpowerx > this.stopPower) {
            this.flowpowerx = this.flowpowerx - this.stopPower;
          } else if (this.flowpowerx < -this.stopPower) {
            this.flowpowerx = this.flowpowerx - -this.stopPower;
          }

          this.windpowerx = this.windpowerx - -(Math.random() * 10).toFixed(0);
        }

        if (this.windpowery > 0) {
          this.flowpowery = this.flowpowery - -this.windpowery * 0.02;
          if (this.flowpowery > this.windpowery) {
            this.flowpowery = this.windpowery;
          }
          if (this.flowpowery > this.stopPower) {
            this.flowpowery = this.flowpowery - this.stopPower;
          } else if (this.flowpowery < -this.stopPower) {
            this.flowpowery = this.flowpowery - -this.stopPower;
          }
          this.windpowery = this.windpowery - (Math.random() * 10).toFixed(0);
        } else {
          this.flowpowery = this.flowpowery + this.windpowery * 0.02;
          if (this.flowpowery > this.stopPower) {
            this.flowpowery = this.flowpowery - this.stopPower;
          } else if (this.flowpowery < -this.stopPower) {
            this.flowpowery = this.flowpowery - -this.stopPower;
          }
          this.windpowery = this.windpowery - -(Math.random() * 10).toFixed(0);
        }

        this.objTop = this.objTop - -this.flowpowerx;
        this.objLeft = this.objLeft - -this.flowpowery;
        if (this.objTop < 0) {
          this.objTop = 0;
          this.windpowerx = (Math.random() * 100).toFixed(0);
        }
        if (this.objLeft < 0) {
          this.objLeft = 0;
          this.windpowerxy = (Math.random() * 100).toFixed(0);
        }
        if (this.objTop > this.config.flowingH - 100) {
          this.objTop = this.config.flowingH - 100;
          this.windpowerx = -(Math.random() * 100).toFixed(0);
        }
        if (this.objLeft > this.config.flowingW - 100) {
          this.objLeft = this.config.flowingW - 100;
          this.windpowerx = -(Math.random() * 100).toFixed(0);
        }
        this.objTop=parseInt(this.objTop);
        this.objLeft=parseInt(this.objLeft);

        // windpowerx: 0,  10> no wind > -10
        // windpowery: 0,
        // stopPower: 10,
        // flowpowerx: 0,
        // flowpowery: 0,
        // console.log("-Q1>>", this.windpowerx, this.windpowery, this.flowpowerx, this.flowpowery);
        // console.log("-top left->>", this.objTop, this.objLeft);

        this.styleflowing = {
          top: this.objTop + "px",
          left: this.objLeft + "px"
        };

        this.$emit("updatetopleft", {
          top: this.objTop,
          left: this.objLeft
        });
        // console.log("---ppp------>>", this.styleflowing);
      }
      // console.log("---QQQ------>>", this.config);
      // console.log("---QQQ1------>>", this.flowingrNumber, this.flowingbelowchartW, this.flowingbelowchartH);
      // if (this.counter > 30) {
      // // // clearInterval(this.interval);
      // this.objTop=  this.config.belowchartH/2 ;
      // this.objLeft= this.config.belowchartW/2 ;
      // console.log("flowinghh  --------> ", this.config);
      // console.log("---ffff------>>",this.objTop,this.objLeft);
      // } else {
      // console.log("--> before", this.chartData.rows);
      //       if (this.flowpowerx <=0){
      //           this.flowpowerx=(Math.random() * 100).toFixed(0);
      //           if((Math.random() * 1).toFixed(0)<1){
      // this.flowpowerx=-this.flowpowerx
      //           }
      //       }
      //       if (this.flowpowery <=0){
      //           this.flowpowery=(Math.random() * 100).toFixed(0);
      //       }
      //       this.objTop= (Math.random() * 500).toFixed(0);
      //       this.objLeft= (Math.random() * 500).toFixed(0);
      // }
    }, 100);
  }
};
</script>

<style scoped>
.flowing {
  position: absolute;
  z-index: +1;
  text-align: "left";
  vertical-align: "top";
}
.flowingbackground {
  position: absolute;
  z-index: +2;
  text-align: "left";
  vertical-align: "top";
  animation-name: therotate;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}
@keyframes therotate { 
  100%  {  -ms-transform: rotate(360deg); /* IE 9 */
  transform: rotate(360deg); }
}
</style>